<template>
<div>
  <Appbar :text="'Neue Kategorie'"> </Appbar>

<!-- without solo -->
  <v-text-field  
    color="gray" v-model="newCategoryName" style="height:10vh;"
    label="Name deiner Kategorie" required clearable/>

  <v-card
    max-width="100vw"
    tile
  >
 <v-row v-for="item of words" :key="item"> 
   <v-col class="col-9">  
     <v-list-item >
      <v-list-item-content> 
          {{item}}          
      </v-list-item-content>
    </v-list-item>
    </v-col>
    
    <v-col class="col-3 centercenter text-center" style="text-align: center; center">
         <v-btn icon small fab ><v-icon small @click="delWort(item)">mdi-close</v-icon></v-btn>
    </v-col>
    </v-row>


    <v-dialog
      v-model="dialog"
      persistent
      max-width="600px"
    >
    
      <v-card>
        <v-card-title>
          <!-- <span class="headline">New Word</span> -->
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col
                cols="12"
                sm="6"
                md="4"
              >
                <v-text-field 
                color="grey"
                isactive= true
                v-model="newWord"
                  label="Neues Wort"
                  required
                ></v-text-field>
              </v-col>
            </v-row>
          </v-container>
          <!-- <small>*indicates required field</small> -->
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="grey darken-1"
            text
            @click="dialog = false"
          >
            Close
          </v-btn>
          <v-btn
            color="gray darken-1"
            text
            @click="saveWord()"
          >
            Save
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>


 

     <!-- <v-btn
              color="primary"
              dark
              class="mb-2"
              v-bind="attrs"
              v-on="on"
            >
              New Item
            </v-btn> -->


            <!-- ***** -->




  </v-card>
<!-- 
    v-bind="attrs"
          v-on="on" -->
  <!-- <template v-slot:activator="{ on, attrs }"> -->
        <v-btn class="mt-5"
        block
          color="gray"
          dark
        @click="dialog = true"
        >
          <v-icon small >mdi-plus</v-icon>
        </v-btn>

        <v-btn to="/" class="mt-2"
          block
          color="grey"
          dark
          @click="finished()"
        >
          Fertig
        </v-btn>
      <!-- </template> -->

      </div>
</template>
<script>
import axios from 'axios';
// "Test1", "Test2", "Test3" 
import Appbar from '@/components/Appbar.vue';
  export default {
    data() {
      return {
        words: [ ],
        dialog: false,
        newWord: "",
        newCategoryName: ""
      }
    },
    components: {
      Appbar
    },
    methods: {
      saveWord() {
        // console.log("saved");
        if(this.newWord != ""){
        this.words.push(this.newWord);

        // history.replaceState({},"","http://localhost:8080/");
        }
        this.newWord = "";
      },
      async finished(){
        console.log("sd");
        await axios.post("http://localhost:3000/categories",{ name: this.newCategoryName, words: this.words})
        // history.go("/");
      },
      delWort(item){
        let ind = this.words.indexOf(item);
        this.words.splice(ind,1);
      }
    },
  }
</script>

<style lang="scss" scoped>
.centercenter{
 justify-content:center; align-items:center; display: flex
}
</style>