<template style="position-absolute">
    <!-- <v-progress-circular
      :rotate="360"
      :size="100"
      :width="15"
      :value="value"
      color="teal"
    >
      {{ value }}
    </v-progress-circular>

    <v-progress-circular
      :rotate="-90"
      :size="100"
      :width="15"
      :value="value"
      color="primary"
    >
      {{ value }}
    </v-progress-circular> -->
    <v-progress-circular
      :rotate="90"
      :size="60"
      :width="5"
      :value="value"
      color="lightgrey"
    >
      {{ ((value/5)*3).toFixed(0) }}
    </v-progress-circular>

    <!-- <v-progress-circular
      :rotate="180"
      :size="100"
      :width="15"
      :value="value"
      color="pink"
    >
      {{ value }}
    </v-progress-circular> -->
  <!-- </div> -->
</template>



<script>


const router = require("../router/index");

  export default {
    data () {
      return {
        rr: router,
        interval: {},
        value: 100,
      }
    },
    beforeDestroy () {
      clearInterval(this.interval)
    },
    mounted () {
      this.interval = setInterval(() => {
        if (Math.round(this.value,0) === 100) {
          // console.log(this.rr.base);
          // console.log("ÜÜÜÜÜÜÜÜÜü")
          // this.rr.go(1);
          // this.rr.replace("/results")
          // router.go(-1);
          return (this.value = 0)
        }
        this.value += (10/6)
        // console.log(this.value);
      }, 1000)
    },
  }
</script>


<style scoped>
.v-progress-circular {
  margin: 1rem;
}
</style>